.pages-list__link {
  display: block;
  padding: 27px 0 27px 75px;

  font-size: 17px;
  line-height: 24px;

  border-top: 1px solid #ececec;
}

@media (min-width: $tablet-width) {
  .pages-list__link {
    display: inline;
    padding: 0;

    font-size: 15px;

    border-top: none;
  }

  .pages-list__item {
    margin-right: 35px;
  }
}

@media (min-width: $desktop-width) {
  .pages-list__item {
    display: flex;
    align-items: center;
    margin-right: 0;

    &:nth-child(odd) {
      border-right: 1px solid #e7e7e7;
    }
  }

  .pages-list__link {
    display: block;
    padding: 0 34px;

    font-size: 17px;
  }
}
